// 所有学生
<template>
  <div class="all">
    <el-button  type="primary" @click="exportSubGrade">导出成绩</el-button>
    <el-table :data="pagination.records" border>
      <el-table-column
        fixed="left"
        prop="studentName"
        label="姓名"
        width="120"
      ></el-table-column>
      <el-table-column
        prop="studentId"
        label="学号"
        width="180"
      ></el-table-column>
      <el-table-column prop="sex" label="性别" width="120"></el-table-column>
      <el-table-column prop="grade" label="年级" width="120"></el-table-column>
      <el-table-column prop="major" label="专业" width="180"></el-table-column>
      <el-table-column prop="clazz" label="班级" width="120"></el-table-column>
      <el-table-column
        prop="etScore"
        label="成绩"
        width="120"
      ></el-table-column>
      <el-table-column
        prop="answerDate"
        label="答题日期"
        width="180"
      ></el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagination.current"
      :page-sizes="[6, 10]"
      :page-size="pagination.size"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total"
      class="page"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      examCode: 0,
      pagination: {
        //分页后的考试信息
        current: 1, //当前页
        total: null, //记录条数
        size: 6 //每页条数
      }
    };
  },
  created() {
    // this.getAnswerInfo();
    this.examCode = this.$route.query.examCode;
    this.showScore();
  },
  methods: {
    // getAnswerInfo() {
    //   //分页查询所有试卷信息
    //   this.$axios(`/api/students/${this.pagination.current}/${this.pagination.size}`).then(res => {
    //     this.pagination = res.data.data;
    //   }).catch(error => {});
    // },
    //改变当前记录条数
    handleSizeChange(val) {
      this.pagination.size = val;
      this.showScore();
    },
    //改变当前页码，重新发送请求
    handleCurrentChange(val) {
      this.pagination.current = val;
      this.showScore();
    },
    exportSubGrade() {
      this.examCode = this.$route.query.examCode;
      let examCode=this.examCode
      window.location.href = 'http://localhost:8080/export/SubExcel/'+ examCode
    },
    // checkGrade(studentId) {
    //   this.$router.push({ path: "/grade", query: { studentId: studentId } });
    // }
    showScore() {
      this.$axios(
        `/api/sc/${this.pagination.current}/${this.pagination.size}/${
          this.examCode
        }`
      ).then(res => {
        //根据试卷id请求后台
        if (res.data.code == 200) {
          this.pagination = res.data.data;
          console.log(res.data.data);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.all {
  padding: 0px 40px;
  .page {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .edit {
    margin-left: 20px;
  }
  .el-table tr {
    background-color: #dd5862 !important;
  }
}
.el-table .warning-row {
  background: #000 !important;
}

.el-table .success-row {
  background: #dd5862;
}
/deep/ .cell {
  text-align: center;
}
</style>



